function Cenovnik(props) {

    const [srbija, setSrbija] = useState(false);
    const [inostranstvo, setInostranstvo] = useState(false);

    return (
    <div> 
      
      <div className="ponudeContainer">
          <div className="ponude" onClick={() => setSrbija(!srbija)}>SRBIJA</div>
             {srbija ? <Srbija /> : null}
      
          <div className="ponude" onClick={() => setInostranstvo(!inostranstvo)}>INOSTRANSTVO</div>
          {inostranstvo ? <Inostranstvo /> : null}
      </div>
    </div>
  );
}

export default Cenovnik;